.App {
  text-align: center;
}
body {
  background: rgb(30, 31, 32);
  background: radial-gradient(
    circle,
    rgba(30, 31, 32, 1) 51%,
    rgba(252, 70, 107, 1) 100%
  );
}
.titlecontainer {
  background-color: white;
  margin: 20px;
  border-radius: 40px;
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.title {
  font-size: 50px;
  font-weight: 800px;
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;

  margin-top: 30px;
}
.inp {
  width: 600px;
  height: 30px;
  font-size: 20px;
  border-radius: 50px;
  padding: 10px;
}
.search {
  margin-top: 80px;
}
.res {
  background-color: white;
  border-radius: 30px;
  margin: 40px;
  align-items: center;
}
.results {
  display: flex;
  justify-content: space-around;
  border-bottom: 2px solid rgb(82, 80, 80);
  border-radius: 80px;
  box-shadow: 1px 1px 10px 1px grey;
  margin: 30px;
}
.coinimage {
  width: 70px;
}
.heading {
  display: flex;
  justify-content: space-around;
}
.heading p {
  font-size: 30px;
  font-weight: bold;
}
.red {
  color: red;
  font-weight: bold;
}
.green {
  color: rgb(21, 242, 21);
  font-weight: bold;
}
